<template>
  <div id="app">
    <div style="">
      <div
        style="display: flex; justify-content: space-between; margin: 0 40px"
      >
        <div>
          PDF转换成
          <el-select v-model="fordata.outputFileType" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div style="display: flex">
          <div style="align-self: flex-end" @click="CopyDownloadPath">
            <span v-if="downloadURL != ''" style="color: blue; cursor: pointer"
              >点击复制下载路径</span
            >
          </div>
          <el-button
            type="primary"
            icon="el-icon-upload2"
            style="width: 200px"
            @click="UploadFile"
            >上传</el-button
          >
        </div>
      </div>
      <div style="box-sizing: border-box; margin: 40px">
        <el-upload
          accept=".pdf"
          action="#"
          :auto-upload="false"
          :on-change="handleChange"
          :on-remove="removeFile"
          list-type="picture"
          drag
          multiple
        >
          <div>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              将文件拖到此处，或<em>点击上传</em>
            </div>
          </div>
        </el-upload>
      </div>
    </div>
  </div>
</template>

<script>
import { FileUpdate, FileUpdate2, FileStatus } from "@/api/file";
import "@/assets/common.css";
import axios from "axios";
let api = window.api;
export default {
  name: "App",
  data() {
    return {
      options: [
        { label: "WORD", value: "docx" },
        { label: "POWERPOINT", value: "pptx" },
        { label: "EXCEL", value: "xlsx" },
      ],
      showUploadList: true,
      fordata: {
        conversionMode: "blocks",
        dpi: 144,
        imageQuality: 85,
        outputFileType: "docx",
        files: [],
      },
      files: [],
      downloadURL: "",
      jobId: "",
      timer: "",
      loading: "",
    };
  },
  methods: {
    //上传前判断
    removeFile(_, filelist) {
      this.files = [];
      for (const file of filelist) {
        this.fordata.file.push(file.raw);
      }
    },
    handleChange(file) {
      this.files.push(file.raw);
    },
    async UploadFile2() {
      this.loading = this.$loading({
        lock: true,
        text: "正在进行转换.......",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      var url;
      if (process.env.NODE_ENV == "development") {
        url = "http://localhost:8888/zh-cn/pdf_to_word";
      } else {
        url = "https://www.ilovepdf.com/zh-cn/pdf_to_word";
      }

      var pdf_to_word = await axios({
        method: "get",
        url: url,
      });
      var html = pdf_to_word.data;
      var match = "ilovepdfConfig.taskId = '(.*?)';";
      match = html.match(match);
      var task;
      if (match.length == 2) {
        task = match[1];
      }
      if (!task) {
        loading.close();
        return this.$message.error("出现错误!!,请联系管理员进行维护!!!");
      }
      this.fordata.task = task;
      let formData = new FormData();
      formData.append("name", this.fordata.name);
      formData.append("chunk", this.fordata.chunk);
      formData.append("chunks", this.fordata.chunks);
      formData.append("task", this.fordata.task);
      formData.append("preview", this.fordata.preview);
      formData.append("pdfinfo", this.fordata.pdfinfo);
      formData.append("pdfforms", this.fordata.pdfforms);
      formData.append("pdfresetforms", this.fordata.pdfresetforms);
      formData.append("v", this.fordata.v);
      formData.append("file", this.fordata.file);
      var res2 = await FileUpdate(formData);
      let formData2 = new FormData();
      formData2.append("convert_to", this.value);
      formData2.append("output_filename", "{filename}");
      formData2.append("packaged_filename", "ilovepdf_converted");
      formData2.append("ocr", 0);
      formData2.append("task", this.fordata.task);
      formData2.append("tool", "pdfoffice");
      formData2.append("files[0][server_filename]", res2.server_filename);
      formData2.append("files[0][filename]", this.fordata.name);
      FileProcess(formData2)
        .then((res) => {
          if (res.error) {
            this.$message.error("转换失败");
          } else {
            this.$message.success("转换成功!!!");
            this.downloadURL =
              "https://api44o.ilovepdf.com/v1/download/" + task;
          }
        })
        .finally(() => {
          loading.close();
        });
    },
    async UploadFile() {
      api.Test()
      return 
      if (this.files.length == 0) {
        return this.$message.warning("文件不能为空!!!");
      }
      this.loading = this.$loading({
        lock: true,
        text: "正在进行转换.......",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      var files = [];
      for (const file of this.files) {
        let formData = new FormData();
        formData.append("file", file);
        var fileInfo = await FileUpdate(formData);
        files.push(fileInfo[0]);
      }
      this.fordata.files = files;
      var res = await FileUpdate2(this.fordata);
      this.jobId = res.jobId;
      this.timer = setInterval(() => {
        FileStatus({
          action: "getStatus",
          jobId: this.jobId,
        }).then((res) => {
          if (res.status == "done") {
            this.loading.close();
            this.$message.success("文件转换完成!!!!");
            this.downloadURL =
              "https://filetools13.pdf24.org/client.php?mode=download&action=downloadJobResult&jobId=" +
              this.jobId;
            clearInterval(this.timer);
          }
        });
      }, 2000);
    },
    CopyDownloadPath() {
      this.$copyText(this.downloadURL).then(
        () => {
          this.$message.success("链接复制成功!!!");
        },
        () => {
          this.$message.error("链接复制失败!!!");
        }
      );
    },
  },
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

